import React from "react";
import axios from "axios";
import {useForm} from "react-hook-form";

const RentBook =()=>{
	
	const {register, handleSubmit, formState:{errors}} = useForm({mode:"onSubmit"});
	
	const onSubmit =(data)=>{
		let {id, callNumber} = data;
		axios({
			url:'/api/rentBook',
			method:'post',
			data:{
				mem_id:id,
				callNumber:callNumber
			},
			baseURL:'http://localhost:8800'
		}).then((res)=>{
			alert(res.data);
			window.location.href='/';
		})
	}
	
	
	return(
		<form onSubmit={handleSubmit(onSubmit)}>
			<ul>
				<li>
				<label></label>
				<input type="text" placeholder="  ." {...register("id",{
					required:"  .",
					pattern: {
						value:/^[A-Za-z0-9]{4,12}$/,
						message:",   4~12  ."
					},
					validate:{
						chekUrl: async (id,check)=> await fetch('/api/check',{
							method:'post',
							headers:{ "Content-type":'application/json; charset=utf-8'},
							body:JSON.stringify(id)
						}).then(res => res.json())
						  .then(res =>{
							console.log(res.result)
							if(res.result === 'pass'){
								check = '  .';
							}
						})|| check,
					}
				})}/>
				<p>{errors.id && errors.id.message}</p>
				</li>
				<li>
					<label></label>
					<input type="text" maxLength="10" placeholder=" ." {...register("callNumber",{
						required:"  .",
						validate:{
							checkUrl: async (callNumber, check)=> await fetch('/api/checkBook',{
								method:'post',
								headers:{ "Content-type":'application/json; charset=utf-8'},
								body:JSON.stringify(callNumber)
							}).then(res => res.json())
							.then(res=>{
								if(!res.result){
									console.log(res.result)
									check = '  .'
								}
							})|| check,
						}
					})}/>
					<p>{errors.callNumber && errors.callNumber.message}</p>
				</li>
			</ul>
			<input type="submit" value=""/>
		</form>
	)
}
export default RentBook;