import React from 'react';
import { useForm } from "react-hook-form";
import axios from 'axios';

const Register =()=>{
	//id  
	
	//form
	const {register, handleSubmit, formState: { errors }} = useForm({mode:"onChange"});
	//console.log(watch());
	// submit
	const onSubmit =(data)=>{
		let {id,passwd,name,cell,email} = data;
		axios({
			url:'/api/register',
			method:'post',
			data:{
				id:id,
				passwd:passwd,
				name:name,
				cell:cell,
				email:email
			},
			baseURL:'http://localhost:8800'
		}).then(function(response){
			alert(response.data);
			document.location.href='/login';
		})
	}
	const onError =(errors)=>{
		console.log(errors);
	}
	return(
		<div className="register-input">
		<form onSubmit={handleSubmit(onSubmit, onError)}>
		<ul>
			<li>
				<label></label>
				<input type="text" name="name" maxLength="5" placeholder="" {...register("name",{
					required : " " //null 
				})}/>
				<p>{errors.name && errors.name.message}</p>
			</li>
			<li>
				<label></label>
				<input type="text" name="id" placeholder="" {...register("id",{
					required: "id ",
					pattern:{
						value:/^[A-Za-z0-9]{4,12}$/,
						message:",  4~12 ."
					},
					validate:{
						checkUrl: async (id, check)=> await fetch('/api/check',{
							method:"post",
							headers : {               //  
              				  "Content-Type":"application/json; charset=utf-8"
           				 },
							body:JSON.stringify(id)
						}).then(res => res.json())
						  .then(res =>{
							console.log(res);
							if(res.result === 'non'){
								check = '    .';
							}
						})|| check,
					}
				})}/>
				<p id="id_check">{errors.id && errors.id.message}</p>
			</li>
			<li>
				<label></label>
				<input type="password" name="passwd" placeholder="" {...register("passwd", {
					required:" "
				})}/>
				<p>{errors.passwd && errors.passwd.message}</p>
			</li>
			<li>
				<label></label>
				<input type="text" name="cell" placeholder="010-1234-1234" maxLength="13" onInput={(e)=>{// - 
					if(e.target.value.length === 11){
						e.target.value = e.target.value.replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3')
					}else if(e.target.value.length === 13){
						e.target.value = e.target.value.replace(/-/g, '')
													   .replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3')
					}
				}} {...register("cell",{
					required:" ",
					pattern:{
						value:/^(01[016789]{1})-[0-9]{3,4}-[0-9]{4}$/,
						message:"   "
					}
				})}/>
				<p>{errors.cell && errors.cell.message}</p>
			</li>
			<li>
				<label></label>
				<input type="email" name="email" placeholder="test@test.com" {...register("email",{
					required:" "
				})}/>
				<p>{errors.email && errors.email.message}</p>
			</li>
				<input type="submit" value=""/>
		</ul>
		</form>
		</div>	
	)
}
export default Register;